"use client";

import React from "react";
import { useTheme, ThemeColor } from "@/lib/theme-context";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Label } from "@/components/ui/label";
import { Switch } from "@/components/ui/switch";
import { Slider } from "@/components/ui/slider";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

export const SettingsPanel = () => {
    const { primaryColor, gravityStrength, setPrimaryColor, setGravityStrength } = useTheme();

    return (
        <Card className="fixed top-24 right-4 w-80 bg-black/80 backdrop-blur-md border-white/20 shadow-2xl z-50">
            <CardHeader>
                <CardTitle className="text-white text-lg">Studio Settings</CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
                {/* Color Theme */}
                <div className="space-y-2">
                    <Label className="text-gray-300">Theme Color ({primaryColor})</Label>
                    <div className="flex gap-2">
                        {(["neon", "red", "blue"] as ThemeColor[]).map((color) => (
                            <button
                                key={color}
                                onClick={() => setPrimaryColor(color)}
                                className={`w-8 h-8 rounded-full border-2 transition-all ${primaryColor === color ? "border-white scale-110" : "border-transparent opacity-50"
                                    }`}
                                style={{
                                    backgroundColor: color === 'neon' ? '#00ffcc' : color === 'red' ? '#ff0055' : '#00ccff'
                                }}
                            />
                        ))}
                    </div>
                </div>

                {/* Gravity Control */}
                <div className="space-y-2">
                    <div className="flex justify-between items-center">
                        <Label className="text-gray-300">Gravity: {gravityStrength.toFixed(1)}</Label>
                        <span className="text-xs text-xs text-gray-500">{gravityStrength < 0.5 ? "Space Mode" : "Normal"}</span>
                    </div>
                    <Slider
                        value={[gravityStrength]}
                        min={0.1}
                        max={2}
                        step={0.1}
                        onValueChange={(val) => setGravityStrength(val[0])}
                        className="[&_.bg-primary]:bg-white"
                    />
                </div>
            </CardContent>
        </Card>
    );
};
